
<div>
  <el-alert type="info" title='版本>=1.1.1支持' show-icon></el-alert>

  切换v-show恢复滚动位置：<el-switch v-model="visible"></el-switch>

  <virtual-scroll
    v-show="visible"
    ref="virtualScroll"
    :data="list"
    :item-size="62"
    key-prop="id"
    @change="(virtualList) => tableData = virtualList">
    <el-table
      :data="tableData"
      border
      row-key="id"
      max-height="500px"
      style="width: 100%">
      <el-table-column label="id" prop="id"></el-table-column>
      <el-table-column label="内容" width="260" prop="text"></el-table-column>
      <el-table-column label="内容省略" width="260" prop="text" show-overflow-tooltip></el-table-column>
      <el-table-column label="详情" min-width="260">
        <template slot-scope="{ row }">
          <el-button type="text" @click="row.show = !row.show;">{{ row.show ? '隐藏' : '显示' }}</el-button>
          <div v-if="row.show">{{ row.text2 }}</div>
        </template>
      </el-table-column>
      <el-table-column prop="date" label="日期" width="150">
      </el-table-column>
      <el-table-column prop="name" label="姓名" width="420">
      </el-table-column>
      <el-table-column prop="province" label="省份" width="320">
      </el-table-column>
      <el-table-column prop="city" label="市区" width="320">
      </el-table-column>
      <el-table-column prop="address" label="地址" width="300">
      </el-table-column>
      <el-table-column prop="zip" label="邮编" width="320">
      </el-table-column>
      <el-table-column fixed="right" label="操作" width="100">
        <template slot-scope="scope">
          <el-button @click="handleClick(scope.row)" type="text" size="small">查看</el-button>
          <el-button type="text" size="small">编辑</el-button>
        </template>
      </el-table-column>
    </el-table>
  </virtual-scroll>
</div>
